* {
    font-family: "Open Sans", sans-serif;
}

body {
    background-color: #f0f0f0;
}

@-webkit-keyframes popupQueen {
    0% {
        transform: scale3d(1, 1, 1);
    }
    50% {
        transform: scale3d(1.5, 1.5, 1.5);
    }
    100% {
        transform: scale3d(1, 1, 1);
    }
}

@keyframes popupQueen {
    0% {
        transform: scale3d(1, 1, 1);
    }
    50% {
        transform: scale3d(1.5, 1.5, 1.5);
    }
    100% {
        transform: scale3d(1, 1, 1);
    }
}
.Qboard {
    position: relative;
    border-radius: 3px;
    padding: 10px;
    width: 100%;
}
.Qboard .q-array-tile {
    display: inline-block;
    margin: 2px;
    text-align: center;
    border-radius: 2px;
}
.Qboard .q-array-tile i.fa-crown {
    font-size: 25px;
    color: #fff;
}

.popupQueen {
    -webkit-animation-name: popupQueen;
            animation-name: popupQueen;
    -webkit-animation-duration: 400ms;
            animation-duration: 400ms;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    -webkit-animation-direction: normal;
            animation-direction: normal;
    transform-origin: center;
    transform-box: fill-box;
    -webkit-animation-timing-function: ease-in-out;
            animation-timing-function: ease-in-out;
    transition: 400ms all;
}

.Qbox {
    display: grid;
    grid-gap: 0 0;
    background-color: #fff;
    grid-template-rows: repeat(var(--Qgrid-rows), 1fr);
    grid-template-columns: repeat(var(--Qgrid-cols), 1fr);

    box-shadow: 0 0.5px 3px rgba(0, 0, 0, 0.12), 0 1px 2px rgba(0, 0, 0, 0.24);
    transition: all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
}

.Qgrid-item {
    padding-top: 100%;
    cursor: pointer;
    text-align: center;
}

.highlight {
    transition: ease-in 300ms;
    transform: scale3d(1.25, 1.25, 1.25);
    margin: 1.5rem;
}

#NQueenResults {
    width: 100%;
    height: 50vh;
    overflow: scroll;
    overflow-x: hidden;
    overflow-y: scroll;
}

.alert-success {
    background-color: #abebc6;
}

* {
    font-family: "Open Sans", sans-serif;
}
.Rboard {
    position: relative;
    border-radius: 3px;
    padding: 10px;
    background-color: #fff;
}

@media only screen and (min-device-width: 320px) and (max-device-width: 900px) {
    .Rboard .r-array-tile {
        display: inline-block;
        margin: 1px;
        text-align: center;
        border-radius: 2px;
        padding: 6px;
    }
    .Rboard .r-array-tile i.rat {
        font-size: 20px;
    }
}

@media only screen and (min-device-width: 901px) {
    .Rboard .r-array-tile {
        display: inline-block;
        margin: 1px;
        text-align: center;
        border-radius: 2px;
        padding: 20px;
    }
    .Rboard .r-array-tile i.rat {
        font-size: 30px;
    }
}

.Rbox {
    display: grid;
    grid-gap: 0 0;
    grid-template-rows: repeat(var(--Rgrid-rows), 1fr);
    grid-template-columns: repeat(var(--Rgrid-cols), 1fr);
    box-shadow: 0 0.5px 3px rgba(0, 0, 0, 0.12), 0 1px 2px rgba(0, 0, 0, 0.24);
    transition: all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
}

.Rgrid-item {
    cursor: pointer;
    text-align: center;
}

@-webkit-keyframes highlightPath {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@keyframes highlightPath {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

.highlightPath {
    -webkit-animation: highlightPath 100ms;
            animation: highlightPath 100ms;
    -webkit-animation-timing-function: ease-in-out;
            animation-timing-function: ease-in-out;
    transition: 100ms all;
}

.flipH {
    transform: scale(-1, 1);
    transform: scale(-1, 1);
}

/* body {
    background-color: #fff !important;
} */

.maskText {
    font-family: "Bebas Neue", cursive;
    font-size: 3rem;
    line-height: 1;
    color: #212121;
    letter-spacing: 2px;
    text-align: center;
    font-weight: 700;
}

.nav-link,
.dropdown-item {
    cursor: pointer;
}

.tabs {
    display: none;
}
.shadowB {
    box-shadow: 0 0.5px 3px rgba(0, 0, 0, 0.12), 0 1px 2px rgba(0, 0, 0, 0.24);
    transition: all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
}

.w-20 {
    width: 230px;
}

.w-20:hover {
    transition: all 0.5s;
    -webkit-filter: sepia(0.7);
            filter: sepia(0.7);
    cursor: pointer;
}

.thumb-title {
    margin-top: 5px;
}

